.header {
	background: no-repeat center/cover url(../images/header/header_bg.jpg);

	z-index: -1;

	min-height: 100vh;

	&__top {
		padding-top: 84px;

		margin-bottom: 120px;


		position: relative;
	}

	&__navigation {
		
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-wrap: wrap;
		z-index: 1;
		width: 100%;
	}


	&__right-block {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&__personal {
		position: relative;

		margin-right: 134px;

		/* 134px */
		&::before {
			position: absolute;

			content: "";

			display: block;

			top: 10px;
			left: 205px;

			width: 48px;
			height: 2px;


			background-color: #ffd68f;
		}
	}



	&__logo {
		position: absolute;
		top: 56%;
		left: 50%;
		transform: translateX(-50%);
		-webkit-transform: translateX(-50%);
		-moz-transform: translateX(-50%);
		-ms-transform: translateX(-50%);
		-o-transform: translateX(-50%);
	}

	&__burger-btn {
		display: block;

		z-index: 1;
		cursor: pointer;
		width: 50px;
		height: 50px;

		position: fixed;
		top: 15px;
		right: 25px;

	}

	&__content {
		@include flex ($jc: center, $ai: center);
		// display: flex;
		// justify-content: center;
		// align-items: center;
		flex-direction: column;
	}


	&__btn {
		font-size: 1.5rem;
		line-height: 2.5em;
		/*40px*/
		letter-spacing: -0.02em;
		text-transform: capitalize;
		border-radius: 35px;


		text-indent: -5.25rem;
		/*84px*/
		position: relative;
		width: 280px;
		height: 70px;

		&::before {
			content: "";

			position: absolute;

			top: 27px;
			/* 27px */
			left: 210px;
			/* 210px */

			width: 30px;
			/* 30px */
			height: 16px;
			/* 16px */

			background: no-repeat center/cover url("../images/arrow.png");
		}
	}


	@media only screen and (max-width: 1440px) {
		.header__personal::before {
			display: none;
		}
	}

	@media only screen and (max-width: 1368px) {
		.header__navigation {
			display: none;
		}

		.header__burger-line,
		.header__burger-line::after,
		.header__burger-line::before {
			background-color: #c7c5c4;
			content: "";
			cursor: pointer;
			width: 35px;
			/*35px*/
			height: 2px;
			/*2px*/
			position: relative;
			display: block;
			transition: all 0.5s ease;
			-webkit-transition: all 0.5s ease;
			-moz-transition: all 0.5s ease;
			-ms-transition: all 0.5s ease;
			-o-transition: all 0.5s ease;
		}

		.header__burger-line::after,
		.header__burger-line::before {
			position: absolute;
			right: 0;
		}

		.header__burger-line::after {
			bottom: 10px;
			/* 10px */

			width: 40px;
			/* 40px */
		}

		.header__burger-line::before {
			top: 10px;
			/* 10px */

			width: 30px;
			/* 30px */
		}

		.header__right-block {
			margin: 160px 0 240px;
		}

		.header__logo {
			display: block;
			top: 120%;
		}
	}

}