@mixin flex ($jc: null, $ai: center)  {
	display: flex;
	justify-content: $jc;
	align-items: $ai;
	flex-wrap: wrap;
}

@mixin font-source-sans($size: false, $colour: false, $weight: false, $lh: false) {
	font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;

	@if $size {
		font-size: $size;
	}

	@if $colour {
		color: $colour;
	}

	@if $weight {
		font-weight: $weight;
	}

	@if $lh {
		line-height: $lh;
	}
}

@mixin colors($text, $background, $border) {
	color: $text;
	background-color: $background;
	border-color: $border;
}